<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default ({
  data () {
    return {
      columns: [
        {
          title: '标签名称',
          key: 'gamename',
          width: 400, 
          align: 'center'
        },
        {
          title: '游戏ID',
          key: 'id',
          width: 300, 
          align: 'center'
        },
        {
          title: '游戏名称',
          key: 'name',
          width: '', 
          align: 'center'
        },
      ],
      data: [],
      fotmData: {

      },
      isLoading: true,
      page: 1,
      per_page: 10,
      totalPages: 0,
    }
  },
  mounted () {
    const token = getToken(),
          tagid = this.$route.query.tagid,
          type = 1,
          params = {"token": token, "page": "1", "per_page": 10,"tagid": tagid, "type":type};

          this.params = params;
          this.getData(params);
  },
  methods: {
    getData(params) {
      api.tagGame(params).then(res => {
        if(res.code === 0) {
          this.totalPages = res.data.totlerows
          this.data = res.data.list
          this.data.forEach(ele => {
            ele.gamename = res.data.tagInfo.name
          });

          this.isLoading = false 
        } else {
          this.$Notice.error({title: res.msg})
        }
      })
    },
    changePage: function(page) {
      this.params.page = page;
      this.getData(this.params);
    },
    changePageNum: function(num) {
      this.params.per_page = num;
      this.getData(this.params);
    },
  }
})
</script>

<template>
  <div>
    <h3><Icon type="md-barcode" size=24 />标签关联游戏列表</h3>
    
    <Table 
        border 
        ref="selection" 
        :columns="columns" 
        :data="data" 
        :loading=isLoading
        >
    </Table>

    <div style="margin: 10px;overflow: hidden">
            <div style="float: right;">
                <Page :total="totalPages*1" show-total show-elevator show-sizer @on-page-size-change="changePageNum" @on-change="changePage" />
            </div>
        </div>
  </div>
</template>

